html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.products-leave {
  clip-path: circle(100%);
}

.products-leave-active {
  position: fixed;
  top: 0;
  left: 50%;
  transition: all 1s ease-in-out;
}

.products-leave-to {
  clip-path: circle(0%);
}

.products-enter {
  clip-path: circle(0%);
}

.products-enter-active {
  transition: all 1.5s ease-in-out;
  position: fixed;
  top: 0;
  left: 50%;
  z-index: 9999;
}

.products-enter-to {
  clip-path: circle(100%);
}

*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #000000;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

section {
  width: 100%;
}

h1, h2, h3, h4 {
  color: #000000;
}

span {
  margin-right: 0.25rem;
}

a {
  color: #000000;
  text-decoration: none;
  text-align: center;
}

ul {
  list-style: none;
}

.container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 85%;
  height: 100%;
}

.main-content {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.link {
  display: flex;
  padding: 1rem;
  font-size: 1.5rem;
  justify-content: center;
  align-items: center;
  text-align: center;
  line-height: 1.5rem;
  width: 15vw;
  background-color: #354a03;
  color: #ffffff;
  align-self: flex-end;
  transition: all 0.3s ease;
}

.link:hover {
  background-color: #CCC61D;
  cursor: pointer;
}

.link--wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
}

.bold {
  font-weight: 600;
  margin-right: 0.5rem;
}

.img-wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.img-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.list-item--margin {
  margin-bottom: 2.5%;
}

.breadcrumbs {
  display: flex;
  margin: 2.5% 0;
  font-size: 1.25vw;
}

.breadcrumbs__link .bold {
  color: #D7E790;
}

.transition {
  display: flex;
  position: fixed;
  z-index: 9999;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  margin: 0;
  pointer-events: none;
}

.transition li {
  transform: scaleY(0);
  background-color: #e65c00;
  transform-origin: bottom left;
  height: 100%;
  width: 20%;
}

.scroll--hidden {
  overflow: hidden;
}

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  pointer-events: none;
  z-index: 9999;
  visibility: hidden;
  opacity: 0;
  background-color: orange;
}

.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.is-cloned {
  position: absolute !important;
  top: 0;
  z-index: 9999;
  width: auto;
  background: black;
}

.title {
  font-weight: 500;
  position: relative;
  padding: 0 0 2.5%;
}

.title:after {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 3px;
  bottom: 0;
  left: 0;
  background-color: rgba(53, 74, 3, 0.5);
}

.scroll-up {
  position: absolute;
  top: 0;
  right: 7.5%;
  transform: translateY(-100%);
  background-color: #434344;
  width: 3.5vw;
  height: 2.5vw;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.scroll-up__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 50%;
  background-image: url("../img/sprite.svg#up-arrow");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

@media screen and (max-width: 1224px) {
  .scroll-up {
    width: 5vw;
    height: 3.75vw;
  }
}

@media screen and (max-width: 1024px) {
  .link {
    width: 17.5vw;
    padding: 0.75rem 1rem;
    font-size: 2vw;
  }
}

@media screen and (max-width: 768px) {
  .link {
    width: 20vw;
    font-size: 2.5vw;
  }
  .breadcrumbs {
    font-size: 2.25vw;
  }
}

@media screen and (max-width: 480px) {
  .link {
    width: 50vw;
    font-size: 4vw;
  }
  .breadcrumbs {
    font-size: 3.5vw;
  }
}

.header {
