  }
}

@media screen and (max-width: 768px) {
  .header-img {
    width: 100%;
    height: 100%;
  }
  .header-img img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
}

.navigation {
  width: 70%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.navigation--wrapper {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

.navigation--wrapper ul {
  display: none;
}

.navigation__item {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
}

.navigation__item:hover > ul, .navigation__item:focus > ul, .navigation__item:active > ul {
  display: inherit;
}

.navigation__link {
  display: flex;
  padding: 2.5rem 0.75rem;
  width: 100%;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-size: 1.25rem;
  transition: all 0.3s ease;
  text-transform: uppercase;
}

.navigation__link:hover, .navigation__link:focus, .navigation__link:active {
  background-color: #354a03;
}

.submenu {
  position: absolute;
  flex-direction: column;
  top: 100%;
  left: 0;
  background-color: rgba(215, 231, 144, 0.5);
}

.submenu__item {
  display: flex;
  width: 100%;
  position: relative;
  flex-direction: column;
}

.submenu__item:hover > ul, .submenu__item:focus > ul, .submenu__item:active > ul {
  display: inherit;
}

.submenu__item ul {
  position: absolute;
  flex-direction: column;
  top: 0;
  left: 100%;
  background-color: rgba(215, 231, 144, 0.5);
}

.submenu__item ul li {
  display: flex;
  width: 100%;
  position: relative;
  flex-direction: column;
}

.submenu__item ul a {
  display: flex;
  padding: 1rem 1.5rem;
  width: 100%;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-size: 1.25rem;
  transition: all 0.3s ease;
  text-transform: uppercase;
}

.submenu__item ul a:hover, .submenu__item ul a:focus, .submenu__item ul a:active {
  background-color: #354a03;
}

.submenu__link {
  display: flex;
  padding: 1rem 1.25rem;
  width: 100%;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-size: 1.25rem;
  transition: all 0.3s ease;
  text-transform: uppercase;
}

.submenu__link:hover, .submenu__link:focus, .submenu__link:active {
  background-color: #354a03;
}

.menu {
  display: none;
  position: relative;
  width: 30px;
  height: 25px;
  margin: auto;
}

.menu:hover {
  cursor: pointer;
}

.menu span {
  margin: 0 auto;
  top: 12px;
  position: relative;
  width: 100%;
  height: 6px;
  background-color: #000000;
  display: block;
}

.menu span:before, .menu span:after {
  position: absolute;
  content: '';
  width: 100%;
  height: 6px;
  background-color: #000000;
  display: block;
}

.menu span:before {
  margin-top: -12px;
}

.menu span:after {
  margin-top: 12px;
}

.menu--toggle {
  margin: 2.5% auto;
}

.menu--toggle span {
  transition-duration: 0s;
  background-color: rgba(0, 0, 0, 0);
  transition-delay: 0.2s;
}

.menu--toggle span:before, .menu--toggle span:after {
  transition-property: margin, transform;
  transition-duration: 0.2s;
  transition-delay: 0.2s, 0s;
  margin-top: 0;
}

.menu--toggle span:before {
  transform: rotate(45deg);
}

.menu--toggle span:after {
  transform: rotate(-45deg);
}

@media screen and (max-width: 1366px) {
  .navigation__link {
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 1224px) {
  .menu {
    display: flex;
  }
  .navigation {
    display: none;
    margin: 0;
  }
  .navigation--active {
    display: flex;
  }
  .navigation--active .navigation--wrapper {
    flex-direction: column;
  }
  .navigation--active .navigation--wrapper > .navigation__item ul {
    transition: all 0.3s ease;
    position: relative;
    left: 0;
    top: 0;
  }
  .navigation--active .navigation--wrapper > .navigation__item ul > li a {
    padding: 2rem 0;
  }
}

