.chooseLanguage {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 7.5%;
}

.chooseLanguage__item {
  display: flex;
  font-size: 1.25rem;
  border: none;
  background-color: transparent;
  position: relative;
  color: #ffffff;
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 0 0.5rem;
  transition: all 0.3s ease;
}

.chooseLanguage__item:hover {
  cursor: pointer;
  color: #354a03;
}

.chooseLanguage__item:nth-child(2n) {
  border-left: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
}

@media screen and (max-width: 1366px) {
  .chooseLanguage {
    width: 10%;
  }
}

@media screen and (max-width: 1224px) {
  .chooseLanguage {
    width: 12.5%;
  }
}

@media screen and (max-width: 1024px) {
  .chooseLanguage {
    width: 17.5%;
  }
}

@media screen and (max-width: 768px) {
  .chooseLanguage {
    width: 22.5%;
  }
}

@media screen and (max-width: 768px) {
  .chooseLanguage {
    width: 30%;
  }
  .chooseLanguage__item {
    width: calc(100% / 3);
  }
}

@keyframes down {
  0% {
    box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.1);
  }
  50% {
    transform: rotateZ(-15deg);
  }
  80% {
    transform: rotateZ(5deg);
  }
  100% {
    box-shadow: none;
  }
}

@keyframes up {
  0% {
    box-shadow: none;
  }
  50% {
    transform: rotateZ(5deg);
  }
  80% {
    transform: rotateZ(-15deg);
  }
  100% {
    box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.1);
  }
}

@keyframes show {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(100%);
  }
  80% {
    transform: translateX(-2.5%);
  }
  100% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
}

@keyframes hide {
  0% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
  20% {
    transform: translateX(5%);
  }
  100% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%);
  }
}

@keyframes showModal {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
}

@keyframes hideModal {
  0% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
  20% {
    transform: translateX(5%);
  }
  100% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%);
  }
}

@keyframes scaleItem {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.025);
  }
}

@keyframes unScaleItem {
  0% {
    transform: scale(1.025);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes opacityEnable {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.slider {
  width: 100%;
  display: flex;
  margin: auto;
  position: relative;
  justify-content: center;
}

.slider--wrapper {
  max-width: 100%;
  width: 100%;
  min-height: 100%;
  height: auto;
  display: flex;
  position: relative;
  overflow: hidden;
  flex-direction: row;
}

.slider__item {
  display: flex;
  flex-direction: column;
  width: 0;
  height: 100%;
  animation: hide 1s ease-in-out forwards;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.slider__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slider__item img:nth-child(even) {
  position: absolute;
  bottom: 0;
  right: 5%;
  width: 40%;
  height: 100%;
  transform: translateY(15%);
}

.slider-btn__item {
  display: flex;
  position: absolute;
  top: 50%;
  left: 0;
  width: 3.5%;
  height: 15%;
  transform: translateY(-50%);
  justify-content: center;
  align-items: center;
  color: black;
  font-size: 1.35rem;
  transition: background-color 0.3s ease-in-out;
  background-color: rgba(255, 255, 255, 0.05);
}

.slider-btn__item:hover {
  background-color: rgba(255, 255, 255, 0.35);
  cursor: pointer;
}

.btn--prev {
  border: 1px solid #354a03;
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
}

.btn--next {
  border: 1px solid #354a03;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
}

.slide-blog {
  display: none;
  flex-direction: column;
  width: 100%;
  padding: 5% 0;
  justify-content: space-between;
}

.slide-blog.activeSlide {
  display: flex;
}

.slide1 {
  background-image: url("../img/lycan.jpg");
}

.slide2 {
  background-image: url("../img/2.jpg");
}

.slide3 {
  background-image: url("../img/anti.jpg");
}

.btn--next {
  left: 100%;
  transform: translate(-100%, -50%);
}

.slider-dots--wrapper {
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10%;
  width: 100%;
}

.slider-dots__item {
  display: flex;
  width: 1.5rem;
  height: 1.5rem;
  margin-left: 1.5rem;
  border: 1px solid #CCC61D;
  border-radius: 50%;
  transition: all 0.3s ease-in-out;
}

.slider-dots__item:first-child {
  margin-left: 0;
}

.slider-dots__item:hover {
  background-color: #CCC61D;
  cursor: pointer;
}

.active {
  background-color: #CCC61D;
}

.activeSlide {
  width: 100%;
  animation: show 1.575s ease-in-out forwards;
}

.arrow {
  width: 100%;
  height: 100%;
  display: flex;
  margin: 0;
  background-position: center;
  background-size: 35%;
  background-repeat: no-repeat;
}

.arrow__left {
  background-image: url("../img/sprite.svg#left");
}

.arrow__right {
  background-image: url("../img/sprite.svg#right");
}

@media screen and (max-width: 768px) {
